import api from '../logic/api'

export default {
  login: async ({ commit }, user) => {
    try {
      user = await api.login(user)
      commit('SET_USER', user.data)
    } catch (err) {
      if (err.response.status === 401) {
        throw new Error('Wrong Username/Password given')
      }
    }
  }
}



